/* pc(1280이상) */
/* main visual */
#visual {
  /* border: 5px solid red; */
  background: #000;
  width: 100%;
  height: 786px;
  position: relative;
}

/* 비주얼 : 랜딩페이지 버튼 */
#visual div.EventText{
  /* border: 3px solid orange; */
  width: 100%;
  height: 100%;
  padding-top: 127px;
  padding-left: 360px;
  position: absolute;
}

#visual div.EventText ul{
  /* border: 3px solid yellowgreen; */
}

#visual div.EventText ul li{
  /* border: 1px solid yellow; */
}

#visual div.EventText ul li:last-child{
  /* border: 3px solid red; */
  margin-top: 11px;
}

#visual div.EventText ul li p{
  /* border: 3px solid rgb(0, 255, 60); */
  font-size: 40px;
  color: #fff;
  font-family: 'HSBombaram';
  font-weight: normal;
  font-style: normal;
  line-height: 51px;
  /* text-shadow: 0 0 6px #b6d8ff, 0 0 11px #b6d8ff, 0 0 16px #b6d8ff, 0 0 40px #b6d8ff; */
  text-shadow: 0px 0px 2px #fff, 0px 0px 20px #b6d8ff; 
}

#visual div.EventText ul li a{
  /* border: 1px solid rgb(0, 255, 60); */
  /* 버튼꾸밈 */
  width: 132px;
  height: 37px;
  background: #4a62b3;
  border-radius: 20px;
  z-index: 5;

  /* 텍스트 */
  font-size: 15px;
  color: #fff;
  font-family: 'S-CoreDream-5Medium';
  font-weight: 500;
  font-style: normal;
  text-align: center;
  position: absolute;
  padding-top: 9px;
}

/* 비주얼 : 스와이프 */
#visual div.swiper-container {
  /* border: 5px solid blue; */
  width: 100%;
  height: 100%;
}

#visual div.swiper-container div.swiper-wrapper div.swiper-slide {
  /* border: 1px solid red; */
  margin-top: 227px;
  width: 1200px;
  height: 421px;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  /* transition:width 2s; */
}

#visual div.swiper-container div.swiper-wrapper div.swiper-slide .mb-vimg {
  display: none;
}

/* ============================================== */
/* visual (태블릿~모바일) : 1024~320 */
@media all and (min-width: 300px) and (max-width: 1024px) {
  #visual {
    /* border: 5px solid red; */
    margin-top: 187px;
    width: 100%;
    height: 654px;
    /* height: 90.83vw; */
  }
  
  /* 비주얼 : 랜딩페이지 버튼 */
  #visual div.EventText{
    /* border: 3px solid orange; */
    display: none;
  }
  
  /* 비주얼 : 스와이프 */
  #visual div.swiper-container {
    /* border: 5px solid blue; */
    position: absolute;
    bottom: 0;
    width: 100%;
  }

  #visual div.swiper-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
  }
  
  #visual div.swiper-container div.swiper-wrapper div.swiper-slide {
    /* border: 1px solid red; */
    width: 100%;
    margin-top: 0;
  
    /* Center slide text vertically */
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    position: relative;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    /* transition:width 2s; */
  }

  #visual div.swiper-container div.swiper-wrapper div.swiper-slide .pc-vimg {
    display: none;
  }

  #visual div.swiper-container div.swiper-wrapper div.swiper-slide .mb-vimg {
    display: block;
  }

  .swiper-pagination-progressbar {
    display: none;
  }
} /* 태블릿 ~ 모바일 종료 */

