	border-radius: 24px;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 80%;
	background: -webkit-linear-gradient(to bottom, rgba(0, 0, 0, 0) 30%, rgba(33, 61, 94, 1) 80%);
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 20%, rgba(33, 61, 94, 1) 70%);
