  position: fixed;
  bottom: 140px;
  right: 20px;
  z-index: 150;
}

#side .scroll-button01 {
  border: 2px solid #666;
  width: 106px;
  height: 58px;
  background: #1a1a1d;
  border-radius: 28px;
  text-align: center;
  position: relative;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, transform 0.2s ease-in-out, visibility 0.3s;
}

#side .scroll-button01 div.light {
  /* border: 1px solid green; */
  display: inline-block;
}

#side .scroll-button01 div.light a {
  /* border: 1px solid green; */
  display: inline-block;
  width: 106px;
  height: 56px;
  padding-top: 8px;
}

#side .scroll-button01 div.light a span {
  /* border: 1px solid green; */
  font-size: 20px;
  color: #fff;
  font-family: 'S-CoreDream-6Bold';
  font-weight: 600;
  font-style: normal;
  padding-top: 7px;
  display: inline-block;
}

#side .scroll-button01 div.dark {
  /* border: 1px solid green; */
  display: none;
  position: absolute;
  top: 0;
}

.scroll-button {
  background: #1a1a1d;
  width: 103px;
  height: 105px;
  border-radius: 25px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, transform 0.2s ease-in-out, visibility 0.3s;
  z-index: 150;
}

.scroll-button.visible,
#scroll-button01.visible {
  /* border: 1px solid red; */
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease;
}

.scroll-button:active,
.scroll-button01:active {
  transform: scale(0.9);
}

.progress-ring {
  /* border: 1px solid red; */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#progressCircle {
    transition: stroke-dashoffset 0.1s linear;
}

