/* Default Styles */
#main-visual {
  background: #000;
  width: 100%;
  height: 786px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.5s ease-in-out;
  overflow: hidden;
}

/* Swiper Styles */
#main-visual .swiper {
  width: 90%;
  height: 100%;
}

#main-visual .swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  max-width: 1200px;
}

#main-visual .swiper-slide img {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
}

/* Swiper Navigation */
.swiper-button-next,
.swiper-button-prev {
  color: #fff;
  font-size: 24px;
}

/* Swiper Pagination */
#main-visual .swiper-pagination {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
}

#main-visual .swiper-pagination-progressbar {
  width: 60%;
  height: 4px;
  background: rgba(255, 255, 255, 0.3);
  position:absolute;
  left:0;
  bottom:40%;
}

#main-visual .swiper-pagination-progressbar-fill {
  background:red;
}

@media (max-width: 1024px) {
  #main-visual {
    height: 600px;
  }

  .swiper {
    width: 95%;
  }
}

@media (max-width: 768px) {
  #main-visual {
    height: 500px;
  }

  .swiper {
    width: 100%;
  }

  .swiper-button-next,
  .swiper-button-prev {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  #main-visual {
    height: 400px;
  }

  .swiper {
    width: 100%;
  }

  .swiper-button-next,
  .swiper-button-prev {
    font-size: 16px;
  }

  .swiper-pagination {
    width: 90%;
  }
}
