/* ***헤더*** */
header {
  /* border: 1px solid red; */
  width: 100%;
  height: 110px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
}

/* 전체 메뉴 배경 */
header div.menu-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: rgba(0, 0, 0, 0);
  backdrop-filter: blur(10px); /* 블러 효과 */
  transition: all 0.3s ease-in-out;
  opacity: 0; /* 처음에는 완전히 투명 */
}

/* 네비게이션에 마우스를 올렸을 때 */
header:hover div.menu-bg {
  height: 300px; /* 원하는 높이 설정 */
  background: rgba(0, 0, 0, 0.6);
  opacity: 1;
}

header div.outer {
  /* border: 3px solid orange; */
  /* 1920 : 1600 = 83.33% */
  width: 83.33%;
  max-width: 1600px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

header div.outer div.inner {
  /* border: 1px solid yellow; */
  width: 1200px;
  height: 100%;
  margin: 0 auto;
  position: relative;
  padding-top : 6px
}

/* 로고 */
header div.outer div.inner h1.pc-logo {
  /* border: 1px solid greenyellow; */
  display: block;
  padding-top: 3px;
  left: 0;
  position: absolute;
}

header div.outer div.inner h1.pc-logo a img {
  /* border: 5px solid rgb(0, 41, 28); */
  width: 191px;
  height: 56px;
}

header div.outer div.inner .tb-logo {
  display: none;
}

/* 내비게이션 */
header div.outer div.inner nav {
  /* border: 1px solid red; */
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: center;
  /* 메뉴를 가운데 정렬 */
  align-items: center;
  padding-top: 28px;
}

header div.outer div.inner nav ul {
  /* border: 3px solid rgb(29, 112, 176); */
  display: flex;
  list-style: none;
  /* gap: 100px; */
  gap: 25px; /* 간격 조정 */
  /* margin-left: 10px; */
}

header div.outer div.inner nav ul li {
  /* border: 1px solid rgb(19, 74, 255); */
  position: relative;
  padding: 10px;
  cursor: pointer;
  text-align: center;

  /* 텍스트 */
  color: #fff;
  font-size: 21px;
  font-family: 'Freesentation-5Medium';
  font-weight: 500;
  font-style: normal;
}

/* 라인 애니메이션 (양쪽에서 중앙으로 모였다가 퍼지는 효과) */
header div.outer div.inner nav ul li::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 0;
  height: 2px;
  background: white;
  transition: width 0.4s ease-in-out, left 0.4s ease-in-out;
}

/* 마우스를 올리면 선이 중앙에서 확장됨 */
header div.outer div.inner nav ul li:hover::after {
  width: 100%;
  left: 0;
}

header div.outer div.inner nav.NavSwiper-container {
  /* border: 1px solid red; */
  display: none;
}

/* 서브 메뉴 스타일 */
.submenu {
  /* border: 1px solid red; */
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%); /* 서브메뉴 중앙 정렬 */
  width: 160px;
  padding-top: 30px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;

  /* 텍스트 */
  font-size: 16px;
  color: #aaa;
  font-family: 'Freesentation-4Regular';
  font-weight: 400;
  font-style: normal;
}

header:hover .submenu {
  opacity: 1;
  visibility: visible;
}

header div.outer div.inner nav ul li div.submenu p {
  /* border: 1px solid red; */
  padding-bottom: 10px;
}

header div.outer div.inner nav ul li div.submenu p:hover {
  text-decoration: underline;
  color: #fff;
  transition: color 0.5s cubic-bezier(0.15, 0.1, 0.25, 1);
}

/* 스크롤 시 스타일 */
.scrolled .menu-bg {
  height: 100px;
  background: rgba(0, 0, 0, 0.4);
  opacity: 1;
}

/* 로그인 메뉴 */
header div.outer div.inner div.right-area {
  /* border: 3px solid cyan; */
  position: absolute;
  right: -183px;
  top: 34px;
  z-index: 10;
}

header div.outer div.inner div.right-area ul#infoMenu {
  /* border: 1px solid rgb(178, 55, 255); */
  float: left;
}

header div.outer div.inner div.right-area ul#infoMenu li {
  /* border: 1px solid rgb(255, 5, 217); */
  display: inline-block;
  padding: 14px 16.5px 0 0;
}

header div.outer div.inner div.right-area ul#infoMenu li:last-child {
  /* border: 1px solid yellow; */
  padding-right: 0;
}

header div.outer div.inner div.right-area ul#infoMenu li a.info1 {
  /* background: rgb(255, 5, 84); */
  color: #fff;
  font-size: 1em;
  font-family: 'WooridaumR';
  font-weight: 400;
  font-style: normal;
}

header div.outer div.inner div.right-area ul#infoMenu li a.info2 {
  background: #003366;
  border: 8px solid #003366;
  border-radius: 20px;
  color: #fff;
  font-size: 14px;
  font-family: 'S-CoreDream-3Light';
  font-weight: 500;
  font-style: normal;
  line-height: 17px;
  margin-right: 18px;
} 

header div.outer div.inner div.right-area ul#infoMenu li a.info2 span{
  /* background: rgb(255, 5, 84); */
  color: #8d98b1;
  padding-left: 1px;
  font-size: 16px;
  font-family: 'yg-jalnan';
  font-weight: normal;
  font-style: normal;
  line-height: 18px;
}

/* 다운로드 버튼 */
.right-area .fancy-button {
  width: 165px;
  height: 44px;
  border: none;
  border-radius: 22px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  /* border: 1px solid red; */
}

header div.outer div.inner button.secondary-btn {
  background: url('../../assets/images/dw_bt02.png') no-repeat center center;
  background-size: cover;
  box-shadow: 0 4px 15px rgba(228, 79, 254, 0.3);

  /* 텍스트 */
  font-size: 17px;
  color: #fff;
  font-family: 'HSBombaram';
  font-weight: normal;
  font-style: normal;
}

header div.outer div.inner button.fancy-button:hover {
  transform: translateY(-3px); /* 공중에 뜨는 효과 */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2); /* 그림자 */
}

header div.outer div.inner button.fancy-button:active {
  transform: translateY(1px);
}

/* 처음부터 움직이는 반짝이는 효과 */
header div.outer div.inner button.fancy-button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: rotate(45deg);
  pointer-events: none;
  animation: shine 3s infinite linear;
}

.tm_header{
  display: none;
}

@keyframes shine {
  0% {
    transform: translateX(-100%) rotate(45deg);
  }
  100% {
    transform: translateX(100%) rotate(45deg);
  }
}

@media all and (min-width: 1400px) and (max-width: 1599px) {
  header div.outer div.inner {
    width: 88%;
  }
}

